class Scanner extends React.Component {
    setRef = webcam => {
        this.webcam = webcam;
    };

    constructor(props) {
        super(props);

        this.state = {
            codigo: false
        }

        this.getScreenshot = this.getScreenshot.bind(this)
    }

    scanImage(dataUri) {
        Quagga.decodeSingle({
            decoder: {
                readers: ["ean_reader"],
            },
            locate: true,
            src: dataUri
        }, function(result) {
            if(result && result.codeResult) {
                this.props.onSuccess(result.codeResult.code)
            } else {
                this.props.onFail({
                    error: "NOT_DECODED"
                })
            }
        }.bind(this));
    }

    getScreenshot() {
        if (this.webcam.getScreenshot()) {
            this.scanImage(this.webcam.getScreenshot())
        } else {
            this.props.onFail({
                error: "NOT_CAPTURED"
            })
        }
    }

    render() {
        return (
            <Layout>
                <Quagga />
                {/*<Webcam
                    heigh={640}
                    width={480}
                    audio={false}
                    ref={this.setRef}
                    screenshotFormat="image/jpeg"
                    videoConstraints={videoConstraints}
                />*/}
                {/*<button type="button" className="btn btn-primary" onClick={this.getScreenshot}>Escanear</button>*/}
            </Layout>
        );
    }
}

export default Scanner;