class QuaggaScan extends Component {
	constructor(props) {
		super(props)

		this.state = {
			codigo: 0
		}
	}

	componentDidMount() {
		App.init()
	}

	render() {
		return(
			<div>
				<div id="interactive" className="viewport"></div>
				<h1>{this.state.codigo}</h1>
			</div>
		)
	}
}

export default QuaggaScan